import React, { useState } from "react";
import { useAuthContext } from "../../../context/auth/AuthContext";
import { useNavigate } from "react-router-dom";
import { MOVIES } from "../../../constants/routes";
import styles from "./searchBar.module.css";
import SearchBarInput from "../../Presentational/Search/SearchBarInput/SearchBarInput";
import SearchBarDropdown from "../../Presentational/Search/SearchBarDropdown/SearchBarDropdown";

const SearchBarWrapper = () => {
  const { state } = useAuthContext();
  const { movies } = state;

  const [value, setValue] = useState("");

  const onSearch = (searchTerm) => {
    setValue(searchTerm);
  };

  const navigate = useNavigate();

  const redirectToMovieDetail = (movie) => {
    navigate(`${MOVIES}/${movie.title}`);
    onSearch(movie.title);
  };

  const handleInputChange = (e) => {
    setValue(e.target.value);
  };

  return (
    <div className={styles.InputContainer}>
      <SearchBarInput value={value} onChange={handleInputChange} />

      <label htmlFor="input" className={styles.labelforsearch}>
        <button className={styles.searchButton} onClick={() => onSearch(value)}>
          <svg viewBox="0 0 512 512" className={styles.searchIcon}>
            <path d="M416 208c0 45.9-14.9 88.3-40 122.7L502.6 457.4c12.5 12.5 12.5 32.8 0 45.3s-32.8 12.5-45.3 0L330.7 376c-34.4 25.2-76.8 40-122.7 40C93.1 416 0 322.9 0 208S93.1 0 208 0S416 93.1 416 208zM208 352a144 144 0 1 0 0-288 144 144 0 1 0 0 288z"></path>
          </svg>
        </button>
      </label>
      {value !== "" && (
        <SearchBarDropdown
          movies={movies}
          value={value}
          redirectToMovieDetail={redirectToMovieDetail}
        />
      )}
    </div>
  );
};

export default SearchBarWrapper;
